import * as uiM from './../managers/uiM.js';
import * as mNM from './../managers/monsterNameM.js';



export function init() {
    uiM.init();
    //Start Menu
    uiM.startMenuStartBtn.click( (e)=>{uiM.setInGameUIPlayerName(uiM.startMenuInput.val())} );
    //In Game Menu
    uiM.setInGameUIEnemyName(mNM.createRandomMName());
    uiM.turnMainBtn.click( (e)=>{uiM.openSmallModalMenu();} );
    //Module Menu
    uiM.closeModalBtn.click( (e)=>{uiM.closeModalMenu();} );
    uiM.tasksListBlock.click( (e)=>{uiM.onTasksListSubmit($(e.target).html());} );
    uiM.readyModalBtn.click( (e)=>{uiM.onAnswerSubmit();} );
    //Small Module Menu
    uiM.smallModalMenu.click( (e)=>{uiM.onSpellSubmit(e);} );
}

