import * as taskM from './taskM';
import {forEach,map,join} from 'lodash-es';
import lM from  './../managers/LevelM';

export let 
    //Start Menu
    startMenuInput,
    startMenuStartBtn,
    //inGame UI
    enemyName,
    playerName,
    enemyH,
    playerH,
    enemyNick,
    turnMainBtn,
    //Modal menu
    modalMenu,
    closeModalBtn,
    taskModalBlock,
    modalInput,
    readyModalBtn,
    desctiptionModalP,
    questionModalP,
    tasksListBlock,
    currTask = null,
    //Small modal menu
    smallModalMenu,
    earthBtn, fireBtn, waterBtn, groundBtn,
    //Game Over Menu
    scoreTable;

export function init (){
    //Start Menu
    startMenuInput = $('#startMenuInput');
    startMenuStartBtn = $('#startMenuStartBtn');
    //inGame UI
    enemyName = $('#enemyName');
    playerName = $('#playerName');
    enemyH = $('#enemyH');
    playerH = $('#playerH');
    enemyNick = $('#enemyNick');
    turnMainBtn = $('#turnMainBtn');
    //Modal menu
    modalMenu = $("#modalMenu");
    closeModalBtn = $("#closeModalBtn");
    desctiptionModalP = $("#descriptionModalP");
    questionModalP = $("#questionModalP");
    modalInput = $("#modalInput");
    readyModalBtn = $("#readyModalBtn");
    tasksListBlock = $("#tasksListBlock");
    //Small modal menu
    smallModalMenu = $("#smallModalMenu");
    earthBtn = $("#earthBtn");
    fireBtn = $("#fireBtn");
    waterBtn = $("#waterBtn");
    groundBtn = $("#groundBtn");
    //Game Over Menu
    scoreTable = $("#scoreTable");

}

// *************************** Start Menu ********************************
export function getStartMenuInputVal() {
    return  $('#startMenuInput').val();
}

// *************************** InGame Menu ********************************
export  function  setInGameUIPlayerName(name){
    let userName;
    if(startMenuInput.val()!= ''){
        userName = name||startMenuInput.val();
    } else {
        userName = 'Tommy <small>z</small>zZ';
    }
    lM.setUserName(userName);
    playerName.html(userName);
};

export function setInGameUIEnemyName(name){
    enemyNick.html(name[1]);
    enemyName.html(name[0]);
}

export function disableTurnMainBtn(){
    turnMainBtn.prop('disabled', true);
}

export function enableTurnMainBtn(){
    turnMainBtn.prop('disabled', false);
}

export function setEnemyH(val){
    enemyH.css("width", val+'%');
}

export function setPlayerH(val){
    playerH.css("width", val+'%');
}




// *************************** Modal Menu ********************************

export function openModalMenu(){
    
    generateTasksListInDomElement(tasksListBlock);
    disableTurnMainBtn();
    disableReadyModalBtn();
    disableModalInput();
    modalMenu.css("z-index","1");
}

export function generateTasksListInDomElement(domEl){
    desctiptionModalP.html("Select:");
    let domList = join(map(taskM.getAvaliableTasks(), (v, k) => {return "<p>"+String(v)+"</p>"} ),'');
    domEl.html(domList);
}

export function onTasksListSubmit(type){
    currTask = taskM.generateTask(type);
    tasksListBlock.html('');
    desctiptionModalP.html(currTask.description);
    questionModalP.html(currTask.question);
    enableModalInput();
    enableReadyModalBtn();
}

export function closeModalMenu(){
    modalMenu.css("z-index","0");
    currTask = null;
    desctiptionModalP.html("");
    questionModalP.html("");
    readyModalBtn.removeClass("correct wrong");
    readyModalBtn.html("Ready");
    modalInput.val("");
    enableModalInput();
    enableReadyModalBtn();
    enableCloseModalBtn();
    enableTurnMainBtn();
}

export function onAnswerSubmit(){
    disableModalInput();
    disableReadyModalBtn();
    disableCloseModalBtn();
    if (taskM.checkAnswer(currTask.answer,modalInput.val())){
        readyModalBtn.addClass("correct");
        readyModalBtn.html("Correct!");
        setTimeout(()=>{closeModalMenu();
            lM.setTaskSolved(true);
            events.trigger('playerTurn');
        },600);
    } else {
        readyModalBtn.addClass("wrong");
        readyModalBtn.html("Wrong!");
        setTimeout(()=>{closeModalMenu();
            events.trigger('enemyTurn');
        },600);
    }
}

export function disableModalInput(){
    modalInput.prop("disabled", true);
}

export function enableModalInput(){
    modalInput.prop('disabled', false);
}

export function disableReadyModalBtn(){
    readyModalBtn.prop('disabled', true);
}

export function enableReadyModalBtn(){
    readyModalBtn.prop('disabled', false);
}

export function disableCloseModalBtn(){
    closeModalBtn.prop('disabled', true);
}

export function enableCloseModalBtn(){
    closeModalBtn.prop('disabled', false);
}


// *************************** Small modal Menu ********************************

export function closeSmallModalMenu(){
    smallModalMenu.css("z-index","0");
}

export function openSmallModalMenu(){
    smallModalMenu.css("z-index","1");
}

export function onSpellSubmit(e){
    console.log($(e.target).html());
    lM.setUserSpell($(e.target).html().toLocaleLowerCase());
    closeSmallModalMenu();
    openModalMenu();
}

//********************************* Game Over Menu *****************************/

export function clearTable () {

}

export function fillTable(userList ){
    forEach(userList, (v,k)=>{
        for (let i = 0; i < 2; i++) {
            console.log("v "+ v[i]);
            scoreTable.children("tbody").children("tr:nth-of-type("+(k+1)+")").children("td:nth-of-type("+(i+1)+")").html(v[i]);
            
        }
    })
}


