
import tD from './../data/tasksData';
import {sample, sampleSize, join, map} from 'lodash-es';
import tasksData from './../data/tasksData';

export class Task  {
    constructor(type){
        this.type = type;
        this.description = this.getDescription(this.type);
        let questionArr = this.getQuestion(this.type);
        this.question = questionArr[0];
        this.devQuestion = questionArr[1];
        this.answer= this.getAnswer(this.devQuestion);
        // this.devAnswer = null;
    }

    getDescription(type){
        return tD[type].description;
    }
}

export class MathsTask extends Task  {
    constructor(){
        super("maths");
    }

    getQuestion(type){
        let t = tD[type];
        let question, devQuestion;
        let n1, n2, sign, copacity;
        n1 = join(sampleSize(t.numbers, +sample(t.copacity)), '');
        n2 = join(sampleSize(t.numbers, +sample(t.copacity)), '');
        sign = sample(t.sign);
        question = join([n1,sign,n2], ' ');
        return [question, question];
    } 

    getAnswer(q){
        return String(eval(q));
    }
}

export class TransTask extends Task {
    constructor(){
        super("translation");
    }

    getQuestion(type){
        let t = tD[type];
        let pair;
        pair = sample(t.pairs);
        return [pair[0],pair];
    }

    getAnswer(q){
        return q[1];
    }
}

export function generateTask(type){
    let task;
    let taskList = {
        "maths": new MathsTask,
        "translation": new TransTask,
    }
    if(type){
        task = taskList[type];
    } else{
        task = sample(taskList);
    }
    
    return task;
}

export function checkAnswer(taskAnswer, userAnswer){
    
    if(taskAnswer === userAnswer){
        return true;
    } else {
        return false;
    }
}

export function getAvaliableTasks(){
    return map(tD, (v, k) => {return k} );
}